
#navi-box {
  width: 100%;
  z-index: 100;
}

#route-button {
  border-radius: 50%;
  width: 60px;
  height: 60px;
  cursor: pointer;
  position: absolute;
  right: 30px;
  bottom: 80px;
  background-image: url("../assets/icon_route_car_white.png");
  background-color: #3385ff;
  background-size: 35px 35px;
  background-repeat: no-repeat;
  background-position: center center;
}

#navi {
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 40px;
  background: #fff;
}

#navi-button1 {
  width: 50%;
  height: 50%;
  position: absolute;
  left: 0px;
  top: 25%;
  cursor: pointer;
  text-align: center;
  color: #3385ff;
  border-right: 1px solid #aaa;
}

#navi-button2 {
  width: 50%;
  height: 50%;
  position: absolute;
  right: 0px;
  top: 25%;
  cursor: pointer;
  text-align: center;
  color: #333;
}

#navi-icon {
  height: 90%;
  vertical-align: middle;
}



